<template>
  <div class="shopstore">
    <div class="bg">
      <p>店铺</p>
    </div>
    <div class="paixu">
      <select @change="paixu">
        <option value="0">默认排序</option>
        <option value="1">按价格排序：从低到高</option>
        <option value="2">按价格排序：从高到低</option>
      </select>
    </div>
    <div class="product">
      <home-product @chuanzhi="ziji" ref="son" class="productt"></home-product>
    </div>
    <div class="footer">
    <foo-ter class="foott"></foo-ter>
    </div>
  </div>
</template>
<script>
import HomeProduct from "../components/HomeProduct";
import FooTer from "../components/Footer"
export default {
  data() {
    return {
      list: null,
    };
  },
  components: {
    HomeProduct,FooTer
  },
  methods: {
    ziji(it) {
      this.$emit("chuanzhi", it);
    },
    paixu(e) {
      this.list = this.$refs.son.product;
      for (var i = 0; i < this.list.length; i++) {
        // console.log(this.list[i]);
        // for (var j = 0; j < this.list[i].length; j++) {
        //   let money = Number(this.list[i].money.slice(1));
        //   this.list[i].money = money;
          if (e.target.value == 1) {
            this.list[i].sort((a, b) => {
              return Number(a.money) - Number(b.money);
            });
          } else if(e.target.value == 2) {
            this.list[i].sort((a, b) => {
              return Number(b.money) - Number(a.money);
            });
          }
         
        // }
      }
    },
  },
};
</script>
<style lang="less" scoped>
.shopstore {
  margin: 30px 0;
  .bg {
    width: 100%;
    height: 400px;
    position: relative;
    background: url(../assets/home/shopbanner.jpg) no-repeat;
    p {
      font-size: 60px;
      position: absolute;
      left: 300px;
      top: 120px;
    }
  }
  .paixu {
    width: 69%;
    margin: 0 auto;
    text-align: right;
    select {
      width: 200px;
      height: 50px;
      font-size: 16px;
      outline: none;
      padding-left: 20px;
      option {
        margin-left: 10px;
      }
    }
  }
  .product {
      margin: 0 auto;
    margin-top: 50px;
    
  }
  .footer{
    margin-top: 50px;
       .foott{
           padding-top: 70px;
      border-top: 1px solid rgb(194, 194, 194);
  }
  }
 
}
</style>